  background-position: center;
}

.browser-support-wrapper > div.browser-safari-cont > div {
  background: url("../../front-end/images/svg/safari.svg") no-repeat;
  background-size: contain;
  background-position: center;
}

.browser-support-wrapper > div.browser-opera-cont > div {
  background: url("../../front-end/images/svg/opera.svg") no-repeat;
  background-size: contain;
  background-position: center;
}

.browser-support-wrapper > div.browser-ios-cont > div {
  background: url("../../front-end/images/svg/ios.svg") no-repeat;
